/* HexDataComponent.css */
.hex-data-container {
    max-width: 100%;
    margin: 5px auto; /* Smaller margin */
    padding: 5px; /* Smaller padding */
    background-color: #1a1a1a; /* Dark background color */
    box-shadow: 0 0 6px rgba(255, 255, 255, 0.1); /* Light box shadow */
    border-radius: 4px; /* Smaller border-radius */
    transition: background-color 0.3s, box-shadow 0.3s; /* Add smooth transitions */
}

.hex-data-section {
    margin-bottom: 5px; /* Smaller margin-bottom */
}

.hex-data-title {
    font-size: 0.8em; /* Smaller font size */
    font-weight: bold;
    margin-bottom: 3px; /* Smaller margin-bottom */
    color: #61dafb; /* Light blue title color */
}

.hex-data-content {
    font-family: 'Arial', sans-serif;
    font-size: 0.6em; /* Smaller font size */
    line-height: 1.2; /* Slightly smaller line-height */
    color: #ffffff; /* White content text color */
    background-color: #333333; /* Dark background color for content */
    padding: 4px; /* Smaller padding */
    border-radius: 3px; /* Smaller border-radius */
    overflow-x: auto; /* Allow horizontal scrolling if needed */
    transition: background-color 0.3s; /* Add smooth background color transition */
}

.hex-data-container:hover,
.hex-data-container:focus {
    background-color: #222222; /* Darker background color on hover/focus */
    box-shadow: 0 0 8px rgba(255, 255, 255, 0.2); /* Slightly increased shadow on hover/focus */
}

/* Mobile Styles */
@media screen and (max-width: 600px) {
    .hex-data-container {
        padding: 3px; /* Smaller padding for mobile */
    }

    .hex-data-title {
        font-size: 0.7em; /* Slightly smaller font size for mobile */
    }

    .hex-data-content {
        font-size: 0.5em; /* Smaller font size for mobile */
    }
}
